import React, { useState } from "react";
import PropTypes from "prop-types";
import { styled } from "@mui/material/styles";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Popover from "@mui/material/Popover";
import IconButton from "@mui/material/IconButton";
import InfoIcon from "@mui/icons-material/Info";

//  
const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor:
      theme.palette.mode === "dark"
        ? theme.palette.grey[900]
        : theme.palette.common.black,
    color: theme.palette.common.white,
    padding: "8px 16px",
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
    color: theme.palette.text.primary,
    padding: "8px 16px",
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

const DataTableContainer = styled(TableContainer)(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark"
      ? theme.palette.grey[800]
      : theme.palette.background.paper,
}));

const DataTable = ({ data }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedField, setSelectedField] = useState(null);

  const handleTogglePopover = (event, field) => {
    setAnchorEl(event.currentTarget);
    setSelectedField(field);
  };

  const handleClosePopover = () => {
    setAnchorEl(null);
    setSelectedField(null);
  };

  const open = Boolean(anchorEl);
  const popoverId = open ? "popover" : undefined;

  const { Username, ...filteredData } = data;
  const entries = Object.entries(filteredData);

  const getMessageForField = (key) => {
    switch (key) {
      case "URL":
        return "  .";
      case "Category":
        return "URL   .";
      case "Reasoncode":
        return "  .";
      case "Reason":
        return "  .";
      case "Referer":
        return " URL   URL .";
      default:
        return "";
    }
  };

  return (
    <DataTableContainer component={Paper}>
      <Table
        sx={{ minWidth: 700, tableLayout: "auto" }}
        aria-label="customized table"
      >
        <TableHead>
          <TableRow>
            <StyledTableCell></StyledTableCell>
            <StyledTableCell></StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {entries.map(([key, value]) => (
            <StyledTableRow key={key}>
              <StyledTableCell component="th" scope="row">
                <div style={{ display: "flex", alignItems: "center" }}>
                  {key}
                  <IconButton
                    size="small"
                    onClick={(e) => handleTogglePopover(e, key)}
                    style={{ marginLeft: "8px" }}
                  >
                    <InfoIcon fontSize="small" />
                  </IconButton>
                </div>
              </StyledTableCell>
              <StyledTableCell>
                {value}
                <Popover
                  id={popoverId}
                  open={open && selectedField === key}
                  anchorEl={anchorEl}
                  onClose={handleClosePopover}
                  anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "center",
                  }}
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "center",
                  }}
                >
                  <div style={{ padding: "10px" }}>
                    <p>{getMessageForField(key)}</p>
                  </div>
                </Popover>
              </StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </DataTableContainer>
  );
};

DataTable.propTypes = {
  data: PropTypes.object.isRequired,
};

export default DataTable;
