import React, { useState, useMemo } from "react";
import { ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import Container from "@mui/material/Container";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Unstable_Grid2";
import Header from "./components/Header";
import Footer from "./components/Footer";
import Caution from "./components/Caution";
import Block from "./components/Block";
import ErrorBoundary from "./components/ErrorBoundary";
import DataTable from "./components/DataTable";
import ButtonGroup from "./components/ButtonGroup";
import { useUrlParams } from "./hooks/useUrlParams";
import getTheme from "./theme";

const App = () => {
  const [darkMode, setDarkMode] = useState(true);
  const params = useUrlParams();

  const theme = useMemo(
    () => getTheme(darkMode ? "dark" : "light"),
    [darkMode]
  );

  const handleThemeChange = () => {
    setDarkMode(!darkMode);
  };

  const URL = params.get("url");
  const Category = params.get("cat");
  const Reasoncode = params.get("reasoncode");
  const Reason = params.get("reason");
  const Username = params.get("user");
  const Referer = params.get("referer");

  const data = {
    URL,
    Category,
    Reasoncode,
    Reason,
    Username,
    Referer,
  };

  const pageTitle =
    Reasoncode === "METHOD_CAUTIONED"
      ? "   "
      : "   ";

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box
        display="flex"
        flexDirection="column"
        minHeight="100vh"
        sx={{ padding: 0 }}
      >
        <Header
          darkMode={darkMode}
          handleThemeChange={handleThemeChange}
          title={pageTitle}
        />
        <Box display="flex" justifyContent="center" sx={{ mt: 4, mb: 4 }}>
          <img
            src={
              darkMode ? "/img/8percent_white.png" : "/img/8percent_purple.png"
            }
            alt=""
            style={{ height: "30px" }}
          />
        </Box>
        <Container maxWidth="lg" sx={{ flexGrow: 1, padding: 0 }}>
          <Grid container spacing={4} direction="column" alignItems="center">
            <Grid item xs={12} sx={{ width: "100%" }}>
              <Paper elevation={3} sx={{ padding: "2vw" }}>
                <ErrorBoundary>
                  {Reasoncode === "METHOD_CAUTIONED" ? (
                    <Caution username={Username} />
                  ) : (
                    <Block username={Username} />
                  )}
                </ErrorBoundary>
                <DataTable data={data} />
                <Box
                  display="flex"
                  justifyContent="center"
                  sx={{ marginTop: "2vh" }}
                >
                  <ButtonGroup referer={Referer} />
                </Box>
              </Paper>
            </Grid>
            <Grid item xs={12}>
              <Footer />
            </Grid>
          </Grid>
        </Container>
      </Box>
    </ThemeProvider>
  );
};

export default App;
